import React from "react";
import { BodyFooterDiv } from "../../../css/CommunityListCSS.js";
import ScrollToTop from "react-scroll-up";

function BodyFooter() {
  return (
    <BodyFooterDiv>
      <ScrollToTop showUnder={160}>
        <span>UP</span>
      </ScrollToTop>
    </BodyFooterDiv>
  );
}

export default BodyFooter;
